.modern-card {
  background: var(--bg-card);
  border-radius: 16px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  transition: all 0.2s ease;
}
.modern-card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}
.modern-card-interactive {
  background: var(--bg-card);
  border-radius: 16px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
  cursor: pointer;
}
.modern-card-interactive:hover {
  box-shadow: var(--shadow-lg);
  transform: translateY(-4px);
  border-color: var(--border-accent);
}
.btn-primary {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-light));
  color: white;
  border: none;
  border-radius: 12px;
  padding: 12px 24px;
  font-weight: 600;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-sm);
}
.btn-primary:hover {
  background: linear-gradient(135deg, var(--primary-dark), var(--primary-color));
  box-shadow: var(--shadow-md);
  transform: translateY(-1px);
}
.btn-secondary {
  background: var(--bg-card);
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
  border-radius: 12px;
  padding: 10px 22px;
  font-weight: 600;
  transition: all 0.2s ease;
}
.btn-secondary:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-1px);
}
.btn-accent {
  background: linear-gradient(135deg, var(--secondary-color), var(--secondary-light));
  color: white;
  border: none;
  border-radius: 12px;
  padding: 12px 24px;
  font-weight: 600;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-sm);
}
.btn-accent:hover {
  background: linear-gradient(135deg, var(--secondary-dark), var(--secondary-color));
  box-shadow: var(--shadow-md);
  transform: translateY(-1px);
}
.modern-input {
  background: var(--bg-card);
  border: 2px solid var(--border-color);
  border-radius: 12px;
  padding: 12px 16px;
  color: var(--text-primary);
  transition: all 0.2s ease;
  font-size: 16px;
}
.modern-input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgb(13 148 136 / 0.1);
}
.modern-input::placeholder {
  color: var(--text-muted);
}
.gradient-primary {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-light));
}
.gradient-secondary {
  background: linear-gradient(135deg, var(--secondary-color), var(--secondary-light));
}
.gradient-hero {
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-color) 0%, var(--primary-color) 0%);
}
.gradient-section {
  background: linear-gradient(135deg, var(--bg-secondary) 0%, var(--bg-tertiary) 100%);
}
.react-tabs {
  -webkit-tap-highlight-color: transparent;
}
.react-tabs__tab-list {
  border-bottom: 5px solid #e4e4e4;
  padding-bottom: 20px;
  margin: 0 0 10px;
}
.react-tabs__tab {
  display: inline-block;
  border: 1px solid transparent;
  border-bottom: none;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 6px 12px;
  cursor: pointer;
}
.react-tabs__tab--selected {
  color: black;
  border-radius: 5px 5px 0 0;
}
.react-tabs__tab--selected:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: linear-gradient(
    160deg,
    rgba(54, 136, 231, 1) 0%,
    rgba(233, 21, 130, 1) 100%
  );
}
.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}
.react-tabs__tab:focus {
  outline: none;
}
.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: linear-gradient(
    160deg,
    rgba(54, 136, 231, 1) 0%,
    rgba(233, 21, 130, 1) 100%
  );
}
.react-tabs__tab-panel {
  display: none;
}
.react-tabs__tab-panel--selected {
  display: block;
